.checkoutcontainer {
  max-width: 90%;
  margin: auto;
  /* border: solid; */
  margin-top: 50px;
}

.checkoutheader {
  display: flex;
  justify-content: space-between;
  align-items:baseline;
}

.checkoutheader h2 {
  margin: 0;
  color: #ffce66;
  font-size: xx-large;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.backbtn {
  background-color: #ffce66;
  /* color: #333; */
  border: none;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  font-weight: 900;
  color: rgb(49, 115, 136);
  margin-bottom: 50px;
}

.checkoutbody {
  background-color: #eee;
  border-radius: 20px;
  /* margin-top: 40px; */
  padding: 25px;
  height: 40vh;
  overflow-y: auto;
}

.checkouttable {
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  text-align: left;
  padding: 8px;
}

th {
  background-color: #ddd;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

.checkoutfooter {
  display: flex;
  justify-content: flex-end;
  /* margin-top: 20px; */
}

.donebtn {
  background-color: #ffce66;
  /* color: white; */
  border: none;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 16px;
  font-weight: 900;
  cursor: pointer;
  margin-top: 50px;
  color: rgb(49, 115, 136)
}